dialog {
    /* border: 너비 스타일 색 */
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    padding: 10px 20px;
}
.task_dialog_container {
    width: 500px;
    display: flex;
    /* flex-direction: column; flex-wrap: nowrap; */
    flex-flow: column nowrap;
    gap: 20px;
    border: none;
}
.task_dialog_container h1 {
    font-size: 20px;
    color: #2d2c2c;
}

.ico_save {
    background-image: url("../images/ico-floppy.svg");
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px !important;
}
.ico_close {
    background-image: url("../images/ico-cross.svg");
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px !important;
}

