@charset "utf-8";
@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');
/* 가변글꼴
@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/variable/pretendardvariable.css');
*/
html, body, iframe { height: 100%; margin: 0 0; padding: 0 0; }

body {
    width: 100%;
    font-family: Pretendard;
} 

/* ------------------ 색상 ------------------ */
.warn { color: #bd6c0f }

/* ------------------ 너비 ------------------ */
.w-50 { width: 50px }
.w-75 { width: 75px }
.w-100 { width: 100px }
.w-150 { width: 150px }
.w-200 { width: 200px }
.w-250 { width: 250px }
.w-300 { width: 300px }

/* 공통 버튼 */
.btn {
    box-sizing: border-box;
    justify-content: center;
    vertical-align: middle;
    padding-top: 5px;
    padding-right: 10px;
    padding-bottom: 5px;
    padding-left: 10px;
    min-width: 56px;
    line-height: 20px;
    font-size: 14px;
    font-family: Pretendard;
    outline-style: none;
    background-color: white;
    color: black;
    /* border: 너비 스타일 색 */
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    /* etc */
    text-decoration-line: none;
}
.btn:hover { filter: brightness(.9); }
.btn.primary {
    background-color: #0f6cbd;
    border: 0px;
    color: white;
}
.btn.primary:hover { filter: brightness(.9); }
.btn + .btn {
    margin-left: 6px;
}
.btn:disabled {
    background-color: #dddddd;
    color: white;
    border-color: #dddddd;
}
.btn:disabled:hover {
    filter: none;
}
.ico_login {
    background-image: url("./images/ico-login.svg");
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px !important;
}
.ico_logout {
    background-image: url("./images/ico-cross.svg");
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px !important;
}
.ibtn {
    box-sizing: border-box;
    padding: 0 0;
    outline-style: none;
    background-color: white;
    color: black;
    /* border: 너비 스타일 색 */
    border: 0px solid #fff;
    border-radius: 4px;
    /* etc */
    text-decoration-line: none;
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
    /* width/height는 이미지를 지정하는 부분에서 정해줘야 한다. */
}
.ibtn:hover { filter: brightness(.9); }
.ibtn:disabled {
    background-color: #dddddd;
    color: white;
}
.ibtn:disabled:hover {
    filter: none;
}
.ibtn + .ibtn {
    margin-left: 6px;
}

/* ------------------- 공통 정렬 ------------------------- */

.al { text-align: right; }
/* 하위 항목들을 적당한 간격으로 나열함 */
.aj {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}


/* --------------------- 폼 박스 ------------------------- */
.formbox {
    border-radius: 4px;
    border: 1px solid #d1d1d1;
    padding: 16px 12px 12px 12px;
    position: relative;
    box-sizing: border-box;
    min-height: 48px;
}
.formbox label:nth-child(1) {
    position: absolute;
    display: inline-block;
    top: -7px;
    background-color: white;
    font-size: 14px;
    color: #a1a1a1;
    padding-left: .5rem;
    padding-right: .5rem;
}
.formbox:focus-within label:nth-child(1) {
    font-weight: bold;
    color: #000;
}
.formbox input[type="text"] {
    font-size: 16px;
    line-height: 16px;
    height: 24px;
    border: none;
    /* input에 focus가 될 때 border를 없애려면 */
    outline: none;
    width: 100%;
    background-color: transparent;
}
.formbox input[type="radio"] {
    line-height: 24px;
}
.formbox input[type="date"] {
    font-size: 16px;
    line-height: 16px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}
.formbox input[type="time"] {
    font-size: 16px;
    line-height: 16px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}
.formbox textarea {
    font-size: 16px;
    line-height: 20px;
    width: 100%;
    height: 96px;
    border: none;
    outline: none;
    background-color: transparent;
}
/* formbox 하단에 붙는 캡션용 */
.formbox_caption {
    padding-left: 12px;
    padding-right: 12px;
    margin-top: 6px;
    color: #656565;
}

.formbox select {
    font-size: 16px;
    line-height: 16px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}

/* ----------------- 메뉴 박스 -------------------- */
.menubox {
    padding: 6px 6px 6px 6px;
    position: relative;
    box-sizing: border-box;
    min-height: 32px;
    font-size: 14px;
}
.menubox label:nth-child(1) {
    position: absolute;
    display: inline-block;
    top: -7px;
    background-color: white;
    font-size: 14px;
    color: #a1a1a1;
    padding-left: .5rem;
    padding-right: .5rem;
}
.menubox:focus-within label:nth-child(1) {
    font-weight: bold;
    color: #000;
}

/* formbox 하단에 붙는 캡션용 */
.menubox_caption {
    padding-left: 12px;
    padding-right: 12px;
    margin-top: 6px;
    color: #656565;
}

.menubox select {
    font-size: 16px;
    font-size: 16px;
    line-height: 16px;
    height: 24px;
    border: none;
    outline: none;
    background-color: transparent;
}

/* --------------------- confirm dialog ------------------------------- */
.confirm-dlg {
    /* border: 너비 스타일 색 */
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    padding: 10px 20px;
}

/* ------------------------- Home -------------------------------- */

.home-main {
    /* vw, vh를 단위를 사용하면 뷰포트 기준으로 크기를 설정할 수 있다. */
    width: 100vw; height: 100vh;
    position: relative;
}

.home-main .area-login {
    width: 200px; height: 200px;
    position: absolute;
    left: calc(50% - 50px);
    top: calc(50% - 50px);
}