.task_main {
    height: 100%;
}

.task_navi {
    position: absolute;
    left: 0; top: 0; right: 0; height: 50px;
    background-color: #ffffff;
    border-bottom: 1px solid #cccccc;
    display: flex;
    flex-flow: row;
    gap: 10px 30px;
    align-items: center;
}

.task_body {
    position: absolute;
    left: 0; top: 51px; right: 0; bottom: 50px;
    overflow-y: scroll;
    padding: 0 0;
}

.task_footer {
    position: absolute;
    left: 0; height: 50px; right: 0; bottom: 0;
    background-color: #eee;
}

.task_container {
    margin: 10px 30px 10px 30px;
    padding: 0 0;
    display: flex;
    /* flex-direction: row; flex-wrap: nowrap; */
    flex-flow: row nowrap;
    /* 아이템 가로 정렬 */
    justify-content: flex-start;
    /* 아이템 세로 정렬 */
    align-items: flex-start;
    gap: 30px;
    list-style: none;
}

/* 첫 번째 카테고리 왼쪽에 간격을 설정한다. gap은 아이템 사이만 설정되기 때문이다. */
