div[popover] {
    /* border: 너비 스타일 색 */
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    padding: 10px 20px;

}
.task_popover_container {
    width: 500px;
    display: flex;
    /* flex-direction: column; flex-wrap: nowrap; */
    flex-flow: column nowrap;
    gap: 20px;
    border: none;
    margin-bottom: 20px;
}
.task_popover_container h1 {
    font-size: 20px;
    color: #2d2c2c;
}
