.task {
    background-color: #ffffff;
    box-sizing: border-box;
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
}

.task * {
    margin: 0 0;
    padding: 0 0;
}

.task h2 {
    width: 200px;
    margin-left: 16px; 
    line-height: 2.3rem;
    padding-bottom: 3px;
    font-size: 14px;
    color: #555555;
    font-weight: normal;
    /*padding-left: .5rem;
    padding-right: .5rem;*/
    /* 줄바꿈 없이 출력하기 */
    white-space: nowrap;
    /* 영역 밖 텍스트 자르기 */
    overflow-x: hidden;
    /* 말줄임표 표시 */
    text-overflow: ellipsis;
    cursor: pointer;
}

.task .title .took {
    width: 16px;
}

.task .due_date span {
    margin-left: 8px;
    /*border-radius: 12px;
    border: 1px solid #777777;*/
    padding: 2px .5rem 3px .5rem;
    font-size: 0.75rem;
    color:cadetblue;
}

.task .detail {
    /* display: none; */
    padding: .5rem 16px;
    font-size: 10px;
}

/* .task .detail.show { display: block; } */

.task .detail .notes {
    font-size: 11px;
    color: #666666;
    max-height: 150px;
    white-space: normal;
    overflow-y: auto;
    word-break: break-all;
}

.task .children_container {
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-right: 10px;
}
