.task {
    background-color: #ffffff;
    box-sizing: border-box;
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
}

.task * {
    margin: 0 0;
    padding: 0 0;
}

.task .title {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    font-weight: 300;
}

.task .title .checker input[type=checkbox] { display: none; }

.task .title .checker input[type=checkbox] + label {
    width: 11px;
    height: 11px;
    background-color: #fff;
    /* inline-block이어야 형태가 유지된다. */
    display: inline-block;
    border-radius: 100%;
    border: 1.5px solid #aaa;
    cursor: pointer;
}

.task .title .checker input[type=checkbox]:checked + label {
    border: 1.5px dotted #fff;
    /* 전환 애니메이션 */
    transition: all 0.7s ease;
}

.task .title h2 {
    width: 200px;
    line-height: 2.3rem;
    padding-bottom: 3px;
    font-size: 14px;
    color: #555555;
    font-weight: normal;
    /*padding-left: .5rem;
    padding-right: .5rem;*/
    /* 줄바꿈 없이 출력하기 */
    white-space: nowrap;
    /* 영역 밖 텍스트 자르기 */
    overflow-x: hidden;
    /* 말줄임표 표시 */
    text-overflow: ellipsis;
    cursor: pointer;
}

.task .title .took {
    width: 16px;
}

.task .due_date span {
    margin-left: .5rem;
    /*border-radius: 12px;
    border: 1px solid #777777;*/
    padding: 2px .5rem 3px .5rem;
    font-size: 0.75rem;
    color:cadetblue;
}

.task .detail {
    /* display: none; */
    padding: .5rem .5rem;
    font-size: 10px;
}

/* .task .detail.show { display: block; } */

.task .detail .notes {
    font-size: 11px;
    color: #666666;
    max-height: 150px;
    white-space: normal;
    overflow-y: auto;
    word-break: break-all;
}

.task .children_container {
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-right: 10px;
}
.task .children li { width: 100%; }
.task .children {
    display: flex /* or inline-flex */;
    flex-direction: column;
    flex-wrap: no-wrap;
    /* 아이템 가로 정렬 */
    justify-content: flex-start;
    /* 아이템 세로 정렬 */
    align-items: flex-start;
    gap: 0px;
    list-style: none;
    padding: 0 0;
}
.task .seperator {
    width: 100%;
    height: 5px;
}
.task .seperator.hover {
    background-color: dodgerblue;
}

.ibtn_ico_morev {
    background-image: url("../images/ico-more-vertical.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 16px 16px;
    width: 16px;
    height: 16px;
}