.task_list {
    /* content-box 모델이므로 width는 padding, border를 뺀 나머지 영역이다. */
    box-sizing: content-box;
    /* 실제 가로 크기는 패딩+테두리를 포함해야 하므로 더 크다 */
    width: 275px;
    padding: 20 20;
}
.task_list h1 {
    font-size: 20px;
    color: #2d2c2c;
}
.task_list_container {
    display: flex /* or inline-flex */;
    flex-direction: column;
    flex-wrap: no-wrap;
    /* 아이템 가로 정렬 */
    justify-content: flex-start;
    /* 아이템 세로 정렬 */
    align-items: flex-start;
    gap: 0px;
    list-style: none;
    padding: 0 0;
}
.task_list_container li { width: 100%; }

.task_list .seperator {
    width: 100%;
    height: 10px;
}
.task_list .seperator.hover {
    background-color: dodgerblue;
}