dialog {
    /* border: 너비 스타일 색 */
    border: 1px solid #d1d1d1;
    border-radius: 4px;
    padding: 10px 20px;
}
.dialog_container {
    width: 500px;
    display: flex;
    /* flex-direction: column; flex-wrap: nowrap; */
    flex-flow: column nowrap;
    gap: 20px;
    border: none;
}
.dialog_container h1 {
    font-size: 20px;
    color: #2d2c2c;
}
.dialog_container table {
    width: 100%;
    font-size: 12px;
    border-collapse: collapse;
}
.dialog_container table thead {
    display: table;
    width: 100%;
}
.dialog_container table tbody {
    display: block;
    max-height: 200px;
    overflow-y: scroll;
    /*border-right: 1px solid #aaa;
    border-bottom: 1px solid #aaa;*/
}
.dialog_container table tbody tr {
    display: table;
    width: 100%;
    box-sizing: border-box;
}
.dialog_container table thead th {
    text-align: center;
}
.dialog_container table td {
    border-top: 0;
    text-align: center;
}

.dialog_container table tr th:nth-of-type(1),
.dialog_container table tr td:nth-of-type(1) {
    width: 50px;
}
.dialog_container table tr th:nth-of-type(2),
.dialog_container table tr td:nth-of-type(2) {
    width: 250px;
}
.dialog_container table tr th:nth-of-type(3) {
    width: calc(100% - 300px);
}
.dialog_container table tr td:nth-of-type(3) {
    border-right: 0;
}

.ico_close {
    background-image: url("../images/ico-cross.svg");
    background-repeat: no-repeat;
    background-position: 10px center;
    padding-left: 35px !important;
}

